@charset "UTF-8";
@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  html,
  body {
    @apply [&::-webkit-scrollbar-thumb]:bg-gray-400
        dark:[&::-webkit-scrollbar-thumb]:bg-slate-500
        [&::-webkit-scrollbar-track]:bg-transparent
        [&::-webkit-scrollbar]:w-3;
  }
}
@layer components {
  .vbox-container {
    @apply overflow-y-auto [&::-webkit-scrollbar-thumb]:bg-gray-400
        dark:[&::-webkit-scrollbar-thumb]:bg-slate-500
        [&::-webkit-scrollbar-track]:bg-transparent
        [&::-webkit-scrollbar]:w-3;
  }
  .alert-success {
    @apply rounded-lg border border-teal-200 bg-teal-100 p-4 text-sm text-teal-800 dark:border-teal-900 dark:bg-teal-800/10 dark:text-teal-500;
  }
  .alert-danger {
    @apply rounded-lg border border-red-200 bg-red-100 p-4 text-sm text-red-800 dark:border-red-900 dark:bg-red-800/10 dark:text-red-500;
  }
}
/* ================================
   HS Select – Mobile Smooth Animation
================================ */
.hs-select-dropdown {
  opacity: 0;
  transform: translateY(-8px);
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.25s ease, transform 0.25s ease, max-height 0.3s ease;
}

.hs-select-open .hs-select-dropdown {
  opacity: 1;
  transform: translateY(0);
  max-height: 320px;
}

/* Mobile optimization */
@media (max-width: 768px) {
  .hs-select-dropdown {
    transition: opacity 0.3s ease, transform 0.3s cubic-bezier(0.4, 0, 0.2, 1), max-height 0.35s ease;
  }
}
/* Touch feedback */
.hs-select-dropdown [role=option] {
  transition: background-color 0.2s ease, transform 0.15s ease;
}

.hs-select-dropdown [role=option]:active {
  transform: scale(0.97);
}

/* ================================
   Profile card hand wave animation
================================ */
@keyframes wave {
  0%, 100% {
    transform: rotate(0deg);
  }
  10%, 30% {
    transform: rotate(14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
}
.animate-wave {
  animation: wave 2.5s infinite;
  transform-origin: 70% 70%;
}